.smallprototype {
  display: grid;
  width: 440px;
  grid-template-columns: 235px 225px;
  grid-template-rows: 90px 30px;
  border-radius: 5px;
  border-bottom: 7px solid #ecf2f9;
  margin: 12px;
  padding: 15px;
}
.backgroundimg {
  display: grid;
  grid-column: 1/2;
  background-repeat: no-repeat;
  background-size: cover;
  grid-row: 1/3;
}
.smalldata {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  grid-row: 1/3;
  text-align: left;
  text-overflow: hidden;
  margin-left: 6px;
}
.smalldatah3 {
  display: grid;
  text-align: left;
}

.bigdata footer {
  display: grid;
  grid-row: 2/3;
  margin: 3px;
}
.smallprototype:hover {
  border-bottom: 4px solid rgb(12, 8, 8);
}
