.bigprototype {
  display: grid;
  width: 820px;
  grid-template-columns: 390px 420px;
  grid-template-rows: 100px 140px;
  border-radius: 5px;
  border-bottom: 7px solid #ecf2f9;
  margin: 15px;
  padding: 10px;
  grid-gap: 12px;
}
.backgroundimage {
  display: grid;
  grid-column: 1/2;
  background-repeat: no-repeat;
  background-size: cover;
  grid-row: 1/3;
}
.bigdata {
  display: grid;
  grid-template-columns: 400px;
  grid-template-rows: auto auto auto;
  grid-row: 1/3;
  text-align: left;
  text-overflow: hidden;
  grid-gap: 2px;
}
.bigdatah3 {
  display: grid;
  text-align: left;
  margin: 3px;
}
.bigdatap {
  display: grid;
  grid-row: 2/3;
  margin: 3px;
}
.p2 {
  margin: 3px;
}

.bigprototype:hover {
  border-bottom: 4px solid rgb(46, 33, 33);
}
