.navigation {
  background-color: #fadcd9;
  margin: 0px;
  padding: 10px;
  color: rgb(12, 8, 8);
}
.span {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 25px;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.navp {
  margin: 5px 0 15px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.lists {
  text-align: center;
  width: 90%;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
}

li:after {
  display: block;
  content: "";
  border-bottom: solid 3px #f79489;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
}
li:hover:after {
  transform: scaleX(1);
}
li.fromRight:after {
  transform-origin: 100% 50%;
}
li.fromLeft:after {
  transform-origin: 0% 50%;
}
