.div1home {
  margin: auto auto;
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
  justify-content: space-evenly;
  text-align: left;
  color: #f9f1f0;
  background-color: #e9ddd4;
}

.bigphoto1 {
  width: 850px;
  height: 540px;
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 75px;
  margin-right: 0px;
}

.smallphoto1 {
  width: 450px;
  height: 270px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 120px;
  margin-top: 15px;
}
.div1home h2 {
  position: relative;
  text-align: left;
  top: 56%;
  left: 22%;
}
.span1 {
  position: relative;
  top: 57%;
  left: 22%;
  opacity: 110;
}
